body{margin:0;background:#b4f3ec;font-family:"Montserrat",sans-serif;color:#243e42}body a:link{color:#243e42;border-radius:5px;padding:2px;background-color:#b4f3ec;color:#243e42;box-shadow:4px 4px calc(4px * 2) #5ce5d6,calc(0px - 4px) calc(0px - 4px) calc(4px * 2) #fff;text-decoration:none}body a:visited{color:#243e42;background-color:#b4f3ec;color:#243e42;box-shadow:2px 2px calc(2px * 2) #5ce5d6,calc(0px - 2px) calc(0px - 2px) calc(2px * 2) #fff}body a:active{background-color:#243e42;color:#b4f3ec;box-shadow:inset 2px 2px calc(2px * 2) #243e42,inset calc(0px - 2px) calc(0px - 2px) calc(2px * 2) #487c84}#root{height:100vh}
.ProjectContainer{display:grid;grid-template-columns:1fr;grid-column-gap:2vw;-webkit-column-gap:2vw;-moz-column-gap:2vw;column-gap:2vw;grid-row-gap:3vh;row-gap:3vh;padding:2%;padding-bottom:4%;justify-content:center}@media only screen and (min-width: 600px){.ProjectContainer{grid-template-columns:1fr 1fr}}@media only screen and (min-width: 1000px){.ProjectContainer{grid-template-columns:1fr 1fr 1fr}}
.Header{background-color:#b4f3ec;color:#243e42;box-shadow:4px 4px calc(4px * 2) #5ce5d6,calc(0px - 4px) calc(0px - 4px) calc(4px * 2) #fff;display:flex;text-decoration:none;font-family:"Raleway",sans-serif;height:50px;font-size:18px}@media only screen and (min-width: 350px){.Header{font-size:calc(30px - (510px - 100vw) * .075)}}@media only screen and (min-width: 510px){.Header{font-size:calc(50px * 0.6)}}.Header ul{margin:0;padding-left:0;width:100%;display:flex;flex-direction:row;align-items:center;justify-content:space-around;list-style-type:none;height:100%}.Header ul li a{text-decoration:none;padding:3px 5px 2px 5px;border-radius:8px;background-color:#b4f3ec;color:#243e42;box-shadow:2px 2px calc(2px * 2) #5ce5d6,calc(0px - 2px) calc(0px - 2px) calc(2px * 2) #fff;color:#243e42}.Header ul li a:hover{background-color:#b4f3ec;color:#243e42;box-shadow:5px 5px calc(5px * 2) #5ce5d6,calc(0px - 5px) calc(0px - 5px) calc(5px * 2) #fff;cursor:pointer}.Header ul li a:active{background-color:#243e42;color:#b4f3ec;box-shadow:inset 5px 5px calc(5px * 2) #243e42,inset calc(0px - 5px) calc(0px - 5px) calc(5px * 2) #487c84;cursor:pointer}
.wrapper .ImageCard,.wrapper .ImageCard.Clickable:active,.wrapper .ImageCard.Clickable:hover,.wrapper .ImageCard.Clickable{background-color:#243e42;color:#b4f3ec;box-shadow:inset 10px 10px calc(10px * 2) #243e42,inset calc(0px - 10px) calc(0px - 10px) calc(10px * 2) #487c84;padding:3%;display:flex;align-items:center;border-radius:12px;-webkit-transition:box-shadow .5s,-webkit-transform .5s ease-in-out;transition:box-shadow .5s,-webkit-transform .5s ease-in-out;transition:box-shadow .5s,transform .5s ease-in-out;transition:box-shadow .5s,transform .5s ease-in-out,-webkit-transform .5s ease-in-out;position:relative}.wrapper .ImageCard .image,.wrapper .ImageCard.Clickable:active .image,.wrapper .ImageCard.Clickable:hover .image{max-height:100%;max-width:100%;object-fit:contain}.wrapper .ImageCard .text,.wrapper .ImageCard.Clickable:active .text,.wrapper .ImageCard.Clickable:hover .text{display:flex;align-items:flex-end;justify-content:flex-end;width:calc(100% - 6% - 20px);height:calc(100% - 9% - 20px);position:absolute;background:rgba(72,124,132,.2);padding:10px;text-align:right;font-size:medium;color:#b4f3ec;visibility:hidden;opacity:0;overflow:hidden;-webkit-transition:opacity .5s,visibility .2s;transition:opacity .5s,visibility .2s}.wrapper .ImageCard.Clickable,.wrapper .Clickable.ImageCard:active,.wrapper .Clickable.ImageCard:hover{background-color:#b4f3ec;color:#243e42;box-shadow:2px 2px calc(2px * 2) #5ce5d6,calc(0px - 2px) calc(0px - 2px) calc(2px * 2) #fff}.wrapper .ImageCard.Clickable:hover{background-color:#b4f3ec;color:#243e42;box-shadow:4px 4px calc(4px * 2) #5ce5d6,calc(0px - 4px) calc(0px - 4px) calc(4px * 2) #fff;cursor:pointer}.wrapper .ImageCard.Clickable:active{background-color:#243e42;color:#b4f3ec;box-shadow:inset 4px 4px calc(4px * 2) #243e42,inset calc(0px - 4px) calc(0px - 4px) calc(4px * 2) #487c84;cursor:pointer}.ImageCard:hover .text{visibility:visible;opacity:1}
.body{font-family:"Montserrat",sans-serif;font-size:calc(1rem + 0.3vw);text-indent:2em;line-height:150%;color:#243e42}.heading{font-size:18px;font-family:"Raleway",sans-serif;text-align:center;line-height:0}@media only screen and (min-width: 350px){.heading{font-size:calc((11vw - 15px) * .75)}}@media only screen and (min-width: 510px){.heading{font-size:calc(50px * 0.75)}}
.IconButtons{background-color:#b4f3ec;color:#243e42;box-shadow:1px 1px calc(1px * 2) #5ce5d6,calc(0px - 1px) calc(0px - 1px) calc(1px * 2) #fff;padding:1vw;border-radius:12px;display:grid;grid-template-columns:auto auto auto;grid-gap:15%;min-width:180px;justify-content:center}.IconButtons a{overflow:hidden;border-radius:10px;padding:8px 30px 6px 8px;background-color:#b4f3ec;color:#243e42;box-shadow:2px 2px calc(2px * 2) #5ce5d6,calc(0px - 2px) calc(0px - 2px) calc(2px * 2) #fff}.IconButtons a div svg{color:#243e42}.IconButtons a:hover{background-color:#b4f3ec;color:#243e42;box-shadow:3px 3px calc(3px * 2) #5ce5d6,calc(0px - 3px) calc(0px - 3px) calc(3px * 2) #fff;cursor:pointer}.IconButtons a:active{background-color:#243e42;color:#b4f3ec;box-shadow:inset 4px 4px calc(4px * 2) #243e42,inset calc(0px - 4px) calc(0px - 4px) calc(4px * 2) #487c84}.IconButtons a:active div svg{color:#b4f3ec}
.Layout{min-width:350px;scroll-behavior:smooth;height:100%}.Layout .header_container{position:-webkit-sticky;position:sticky;top:0;width:100%;z-index:99}.Layout .Body{height:calc(100% - 50px)}
.Layout body{display:flex;flex-direction:column;align-items:center}.Layout body h1{font-family:"Raleway",sans-serif}.Layout body p{font-family:"Montserrat",sans-serif}
.Contact{padding:2%;display:grid;grid-template-rows:auto auto auto;justify-items:center;align-items:center;height:calc(100% - 4% - 50px)}.Contact .card_container{width:80vw}@media only screen and (min-width: 400px){.Contact .card_container{width:25vw;min-width:320px}}.Contact .text_container{width:100%}@media only screen and (min-width: 545px){.Contact .text_container{width:80vw;min-width:545px}}.Contact .button_container{display:flex;width:100%;justify-content:center}
.About{margin:2%;display:flex;flex-flow:row wrap;align-items:center;justify-content:space-around}.About>*{margin:1%;min-width:330px;flex-basis:100vw}.About .profile_text .heading{overflow:hidden;height:0}.About .profile_picture{flex-basis:60vw}@media only screen and (min-width: 888px){.About .profile_picture{flex-basis:35vw}.About .profile_text{flex-basis:55vw}.About .profile_text .heading{height:auto}.About .project_picture{flex-basis:45vw;order:2}.About .project_text{flex-basis:45vw}}@media only screen and (min-width: 1000px){.About .end_picture{order:2}}
.Cube{margin:2%;display:flex;flex-flow:row wrap;align-items:center;justify-content:space-around;font-size:large;text-indent:2em}.Cube>*{margin:1%;min-width:330px;flex-basis:100vw}@media only screen and (min-width: 600px){.Cube .main_picture{flex-basis:35vw}.Cube .main_text{max-width:55vw}.Cube .end_picture{max-width:45vw}.Cube .end_text{max-width:45vw}}@media only screen and (min-width: 1000px){.Cube .end_picture{order:2}}@media only screen and (min-width: 1250px){.Cube{font-size:x-large}}
